/* Start */
.screen-container{
    background-color: #20232a;
    width: 100%;
    height: 100vh;
    text-align: center;
}
.screen-content{
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}
.screen-title{
    font-size: 2.6rem;
    font-weight: 600;
    color: #fff;
    padding: 12px 0 12px 0;
}
.btn-screen{
    font-size: 1.25rem;
    width: 100%;
    padding: 12px 0 12px 0;
    background-color: #61dafb;
    border: 1px solid #61dafb;
    color: #fff;
    transition: 0.2s;
    margin-bottom: 12px;
}
.btn-screen:hover{
    background-color: #61dafb;
    border: 1px solid #61dafb;
}
.screen-text{
    font-size: 1.25rem;
    font-weight: 400;
    color: #fff;
    padding-bottom: 12px;
}
.score{
    color: #61dafb;
}
.score-top{
    color: #e0f8ff !important;
}

/* Navbar Styling */
.navbar{
    background-color: #61dafb;
    padding: 10px;
}
.navbar-brand{
    font-weight: 500;
}
.question-container{
    background-color: #20232a;
    padding: 40px;
}
.question-number{
    font-size: 1.25rem;
    font-weight: 400;
    color: #61dafb;
}
.question{
    font-size: 2.6rem;
    font-weight: 600;
    color: #fff;
}

/* Form Styling */
.form{
    margin-top: 12px;
}
.form label{
    color: #20232a;
    background-color: #f7f7f7;
    border: 1px solid #ececec;
    font-size: 1.25rem;
    width: 100%;
    cursor: pointer;
    transition: 0.2s;
    text-align: center;
    padding: 12px 0 12px 0;
}
.form label:hover{
    background-color: #ececec;
    transition: 0.2s;
}
input[type="radio"]{
    display: none;
}
input[type="radio"]:checked+label{
    background-color: #61dafb;
    border: 1px solid #61dafb;
    color: #fff;
}

/* Button Styling */
.btn-continue{
    font-size: 1.25rem;
    width: 100%;
    padding: 12px 0 12px 0;
    background-color: #20232a;
    border: 1px solid #20232a;
    color: #fff;
    transition: 0.2s;
    margin-bottom: 12px;
}
.btn-continue:hover{
    background-color: #20232a;
    transition: 0.2s;
}